html {
  width: 100%;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
}

p {
  margin: 10px;
}

svg {
  z-index: -1;
}

.modal {
  position: relative;
  width: 200;
  height: 200;
  z-index: 10;
  background-color: 'red';
}

.content {
  display: none;
}

.content--active {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.loading {
  margin: auto;
  text-align: center;
  line-height: 50;
}

.controller-container {
  display: flex;
  flex-direction: column;
  padding-top: 10px;
  padding-right: 10px;
  padding-left: 10px;
  /* flex: 1; */
}

.controller-container-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  /* flex: 1; */
}

.tight-on-right {
  margin-right: 10px;
}

.d3-container {
  outline: gray solid 1px;
  margin: 10px;
}

